<template>
  <div class="group-top">
    <div class="row cover">
      <b-col class="p-0" cols="12">
        <img
          v-if="group.cover"
          class="cover"
          :src="'https://mysterious-brook-79437.herokuapp.com/tmp_images/' + group.cover"
        />
      </b-col>
    </div>
    <div class="row content mt-5 pt-4">
      <b-col cols="12">
        <div class="group-name h3 fw-bold">
          {{ group.name }}
        </div>
        <div class="statistic d-flex">
          <div class="type-group" v-if="group.type == config.group.type.public">
            <b-icon icon="globe"></b-icon> Nhm cng khai
          </div>
          <div v-else class="type-group">
            <b-icon icon="lock"></b-icon> Nhm ring t
          </div>
          <div class="mx-4">{{count.person}} thnh vin</div>
          <div>{{count.post}} bi vit</div>
        </div>
        <button
          v-if="!member.role && (!member.status)"
          class="btn btn-outline-success mt-4"
          style="float: left"
          v-on:click="answerGroup()"
        >
          Tham gia nhm
        </button>
        <button
          v-if="member.status == config.member.status.pending"
          class="btn btn-outline-secondary mt-4"
          style="float: left"
          v-on:click="outGroup()"
        >
          Hy yu cu
        </button>
        <button
          v-if="member.status == config.member.status.member"
          class="btn btn-outline-danger mt-4"
          style="float: left"
          v-on:click="confirmOutGroup()"
        >
          Ri khi nhm
        </button>
      </b-col>
    </div>
    <div class="row navbars">
      <b-col cols="6">
        <b-row>
          <b-col
            cols="3"
            class="cursor-pointer nav-bar-top time-line p-3 fw-bold"
            v-on:click="changeComponent(1)"
            >Tho lun</b-col
          >
          <b-col
            cols="3"
            class="cursor-pointer nav-bar-top introduce p-3 fw-bold"
            v-on:click="changeComponent(2)"
            >Gii thiu</b-col
          >
          <b-col
            cols="3"
            class="cursor-pointer nav-bar-top introduce p-3 fw-bold"
            v-on:click="changeComponent(3)"
            v-if="group.type==config.group.type.public || member.status == config.member.status.member"
            >Thnh vin</b-col
          >
          <b-col
            cols="2"
            class="cursor-pointer nav-bar-top introduce p-3 fw-bold"
            v-on:click="changeComponent(4)"
            v-if="group.type==config.group.type.public || member.status == config.member.status.member"
            >nh</b-col
          >
        </b-row>
      </b-col>
      <div v-if="modalAnswer">
        <div class="modal-answer"></div>
        <div class="modal-dialog-answer">
          <div class="modal-content-answer" v-click-outside="closeModalAnswer">
            <div class="header-modal-answer">
              <span style="float: left" class="h4 fw-bold mx-2"
                >Tr li cu hi</span
              ><b-icon
                icon="x-circle"
                v-on:click="closeModalAnswer"
                style="float: right"
              ></b-icon>
            </div>
            <br />
            <br />
            <div class="content-modal-answer">
              <p style="text-align: left">
                Hy tr li nhng cu hi sau ca qun tr vin  h c th xem
                xt yu cu tham gia ca bn. Cu tr li ch hin th vi qun
                tr vin v ngi kim duyt
              </p>
              <div
                class="question pb-4"
                v-for="(question, index) in questions"
                :key="index"
              >
                <span class="fw-bold">Cu s {{ index + 1 }}:&nbsp;&nbsp;</span>
                <span>{{ question }}</span>
                <div class="form-group px-2">
                  <textarea
                    class="form-control"
                    placeholder="Vit cu tr li  y"
                    v-model="answers[index - 1]"
                  ></textarea>
                </div>
              </div>
              <div style="text-align: right" class="px-2">
                <div class="btn btn-success" v-on:click="participationGroup">
                  Gi
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
// @ is an alias to /src
import Axios from "@/components/Axios.js";
import config from "@/config";
import EventBus from "@/EventBus.js";
export default {
  name: "GroupTop",
  props: {
    count: Object,
    group_: Object,
    member_: Object,
  },
  created() {
  },
  mounted() {

  },

  data() {
    return {
      config: config,
      component: 1,
      modalAnswer: false,
      answers: [],
      questions: JSON.parse(this.group_.question),
      group: this.group_,
      member: this.member_,
    };
  },
  watch: {},

  methods: {
      changeComponent(index) {
        EventBus.$emit('changeComponentGroup', index)

      },
    answerGroup() {
      if (this.group.type == this.config.group.type.private) {
        this.modalAnswer = true;
      this.member.status = config.member.status.pending
      } else {
        this.participationGroup();
      this.member.status = config.member.status.member

      }
    },
    outGroup() {
      this.member.status = null

      Axios.post("group/out-group", { id: this.group.id })
        .then((response) => {
          if (response.data.status === "success") {
            EventBus.$emit("infoMember", {});
          } else {
            alert(response.data.message);
          }
          this.ajaxLock = false;
        })
        .catch(() => {
          alert(" c li xy ra, vui lng th li sau1");
          this.ajaxLock = false;
        });
    },
    confirmOutGroup() {
      var a = confirm("Bn c chc l s ri nhm");
      if (a) {
        this.outGroup();
      }
    },
    closeModalAnswer() {
      this.modalAnswer = false;
    },
    participationGroup() {
      this.modalAnswer = false;
      this.answers = {};
      var ob = {
        id: this.group.id,
      };
      var quesAndAnswer = [];
      if (this.group.type == this.config.group.type.private) {
        this.questions.forEach((ques, index) => {
          quesAndAnswer.push({
            question: ques,
            answer: this.answers[index],
          });
        });
        ob.answer = JSON.stringify(quesAndAnswer);
      }
      Axios.post("group/participation", ob)
        .then((response) => {
          if (response.data.status === "success") {
            EventBus.$emit("infoMember", response.data.data);
          } else {
            console.log(response.data.message);
          }
          this.ajaxLock = false;
        })
        .catch(() => {
          alert(" c li xy ra, vui lng th li sa4u");
          this.ajaxLock = false;
        });
    },
  },
};
</script>
