var parse = {
  parseDate(date) {
    var dateParse = date.split("-");
    return dateParse[2] + "/" + dateParse[1] + "/" + dateParse[0];
  },
  parseEducation(education, short = false) {
    var status, level;
    if (education[5] || education[4]) status = " hc ti";
    else status = "ang hc ti";
    switch (education[0]) {
      case "1":
        level = "Tiu hc";
        break;
      case "2":
        level = "Trung hc c s";
        break;
      case "3":
        level = "Trung hc ph thng";
        break;
      case "4":
        level = "i hoc";
        break;
      default:
        level = "Cao hoc";
        break;
    }
    if (!short)
    return (
      status +
        " " +
        level +
        " " +
        " <span class='fw-bold'>" +
        education[1]
      + "</span> t" +
        (education[2] ? education[2] + "/" : "") +
        education[3] +
        " n " +
        (education[2] ? education[4] + "/" : "") +
        education[5] ?? "nay"
    );
    else return (
      status +
        " " +
        level +
       " <span class='fw-bold'>" +
      education[1]
    + "</span>"
    )

  },
  parseWorkplace(education, short = false) {
    var time = education[2].split("-");
    var status;
    if (time.length == 1) status = "ang lm ";
    else status = " lm ";
    if (!short)
    return (
      status +
      " " +
      education[0] +
      " ti <span class='fw-bold'>" +
      education[1]
    + "</span> t " +
      time[0] +
      " n " +
      (time[1] ?? "nay")
    );
    else return (
      status +
      " " +
      education[0] +
      " ti <span class='fw-bold'>" +
      education[1]
    + "</span>"
      )
  },
};
export default parse;
